import React, { Component } from "react";
import RickandMorty from "../Homepage";

import "../styles/styles.css";

class RickAndMortyAPI extends Component {
  constructor() {
    super();
    this.state = {
      isLoading: false,
      character: [],
      page: 1,
      info: [],
    };
  }

  fetchData = async (page) => {
    this.setState({
      isLoading: false,
    });

    const res = await fetch(
      `https://rickandmortyapi.com/api/character/?page=${page}`
    );
    const data = await res.json();
    setTimeout(() => {
      this.setState(() => {
        return {
          isLoading: true,
          character: data.results,
          info: data.info.pages,
        };
      });
    }, 300);
  };

  componentDidMount = () => {
    this.fetchData(this.state.page);
  };

  nextClick = () => {
    this.fetchData(
      this.state.page === this.state.info ? 1 : this.state.page + 1
    );
    this.setState({
      page: this.state.page === this.state.info ? 1 : this.state.page + 1,
    });
  };

  prevClick = () => {
    this.fetchData(
      this.state.page <= 1 ? this.state.info : this.state.page - 1
    );
    this.setState({
      page: this.state.page <= 1 ? this.state.info : this.state.page - 1,
    });
  };

  render() {
    return (
      <div>
        <RickandMorty
          data={this.state}
          prev={this.prevClick}
          next={this.nextClick}
        />
      </div>
    );
  }
}

export default RickAndMortyAPI;
